export const priceValite = (val: string, isGuaCar = false): boolean => {
  if (isGuaCar) {
    if (/^\d+\.$/.test(val)) {
      return false;
    }
    if (Number(val) == 0) {
      return true;
    }
  }

  if (Number(val) == 0) {
    return false;
  }

  const regx = /^[1-9]\d{0,7}$/;
  if (!/^[0-9]{1,8}([.]*\d{1,2})*$/.test(val)) {
    return false;
  } else {
    if (val.indexOf(".") != -1) {
      const intNum: string[] = val.split(".");
      if (intNum[0].length > 1) {
        return !!(
          intNum[0].match(regx) &&
          intNum[1].length <= 2 &&
          intNum[0].length <= 8
        );
      } else {
        return intNum[0] == "0" && intNum[1].length <= 2;
      }
    } else {
      return !!val.match(regx);
    }
  }
};

// 100100 99
export const regexMoneyRate =
  /^(99|(([1-9]){1}[0-9]?|0{1})((\.)([0-9]){1,2})?)$/;

export const dkjeImpl = (val: string, isGuaCar = false): boolean => {
  const regx = /^[1-9]\d*$/;
  if (!isGuaCar) {
    return regx.test(val) && val != "0";
  } else {
    return /(^[1-9]\d*$)|(^[0]$)/.test(val);
  }
};
