import axios, { AxiosResponse } from "axios";
import { configure } from "axios-hooks";
import has from "lodash/has";
import { requsetDataEncrypt, responseDecrypt } from "utils/crypt";
import { pickBy } from "lodash";
import DeviceInfo from "constants/DeviceInfo";
import { API_FILE_UPLOAD } from "constants/apiPath";
import { logout } from "utils/user";
import { navigateToTop } from "navigation/RootNavigation";

const encryptEnable = false;
const ENCRYPT_METHODS = ["POST", "post"];
const EXTRA_API = [API_FILE_UPLOAD]; // 
export const baseURL = DeviceInfo.isWeb
  ? "/api"
  : "http://gateway.ifspdev.k5.bigtree.tech";

export const requestInstance = axios.create({
  baseURL,
  timeout: 600000,
  headers: {
    "X-Requested-With": "XMLHttpRequest",
    "Content-Type": "application/json",
  },
  responseType: "json",
});

configure({ axios: requestInstance });

requestInstance.interceptors.request.use(
  async (config) => {
    if (USER_TOKEN) {
      config.headers["access-token"] = USER_TOKEN;
    }
    const { data, method, url } = config || {};
    if (
      encryptEnable &&
      data &&
      ENCRYPT_METHODS.includes("" + method) &&
      !EXTRA_API.find((api) => url!.includes(api))
    ) {
      let jsonData;
      if (Array.isArray(data)) {
        jsonData = JSON.stringify(data);
      } else {
        jsonData = JSON.stringify(
          pickBy(data, (value) => value !== null && value !== undefined)
        );
      }
      console.log(`${url}  >>>>>`, jsonData);
      const encryptData = requsetDataEncrypt(jsonData);
      config.data = encryptData;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

requestInstance.interceptors.response.use(
  (response) => {
    return codeHandle(response);
  },
  async (error) => {
    if (!has(error, "config")) {
      console.warn("");
      return Promise.reject(error);
    }
    // ToastTool.show('');
    // requestLogger(error);
    const config = error?.config;
    if (error?.response?.status === 401 && !config._retry) {
      config._retry = true;
      return axios(config);
    }
    return Promise.reject(error);
  }
);

const codeHandle = (response: AxiosResponse) => {
  const { data: responseData, config, status } = response || {};
  const { code, message, data } = responseData || {};
  console.log("responseData >>>", responseData);
  if (encryptEnable) {
    // <<<<<  >>>>>>
    const { url, method } = config || {};
    if (data && ENCRYPT_METHODS.includes(method as string)) {
      const { bizData, signature, seqNo, encryptKey, version } = data || {};
      const decryptData = responseDecrypt(
        encryptKey,
        bizData,
        seqNo,
        version,
        signature
      );
      if (typeof decryptData === "string") {
        responseData.data = JSON.parse(decryptData);
      }
    }
  }
  const typeCode = String(code).substr(0, 1);
  const productCode = String(code).substr(1, 2);
  const messageCode = String(code).substr(3, 3);

  if (code === 116401) {
    logout();
    navigateToTop();
    return Promise.reject(responseData);
  }

  // typeCode: 2=, 3=, 4=c;  toast , 
  if (parseInt(typeCode) == 4) {
    toast.show(message);
    return Promise.reject(responseData);
  }

  return Promise.resolve(responseData);
};

